<template>
  <div ref="router-view-wrapper" @click="goFullScreen">
  <router-view></router-view>
  </div>
</template>
<script>
export default {
  data(){
    return {
      isFullScreen: false
    }
  },
  methods:{
    goFullScreen(){
      if(this.isFullScreen){
        this.exitFullScreen();
      } else {
        const appEl = document.getElementById('app');
        appEl.requestFullscreen();
      }
      this.isFullScreen = !this.isFullScreen;
    },
    exitFullScreen(){
      document.exitFullscreen();
    }
  }
}
</script>
